import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import StdInfoComp from './StdInfoComp';

import StdBox from './StdBox';
import ExplainBoxComp from 'Components/ExplainBoxComp';
import level from 'const';

/* styled-components */
const Container = styled.div`
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding: 3% 4.5% 4% 4.5%;
`;

const BoxContainer = styled.div`
    display: flex;
    width: 95%;
    justify-content: flex-end;
    align-self: flex-end;
`;

const InfoBox = styled.div`
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
`;

const StdInfo = ({ student, goBack, amount, score, voice_score }) => {
    const { color, text } = level.level3;

    return (
        <Container>
            {/* TODO
              UI  */}
            <BoxContainer><StdBox student={student} goBack={goBack} /></BoxContainer>
            <InfoBox>
                <ExplainBoxComp Content={<StdInfoComp
                    title={' '}
                    score={amount && amount + ' '} />} />
                <ExplainBoxComp
                    Content={<StdInfoComp
                        title={''}
                        color={color}
                        text={text}
                        per={!!score ? score + '%' : '0%'}
                        score={score && score + '%'} />} />
                <ExplainBoxComp
                    Content={<StdInfoComp
                        title={' '}
                        color={color}
                        text={text}
                        per={!!voice_score ? voice_score + '%' : '0%'}
                        score={voice_score && voice_score + ' '} />} />
            </InfoBox>
        </Container >
    );
}

export default StdInfo;